import { createRoot } from "react-dom/client";
import React, { useEffect, useState } from 'react';
import Papa from 'papaparse';
import {
    Container,
    Box,
    Typography,
    Button,
    FormControl,
    FormLabel,
    RadioGroup,
    FormControlLabel,
    Radio,
} from '@mui/material';
import Chart from 'chart.js/auto';



interface Question {
    id: string;
    Question: string;
    Value1: string;
    Answer1: string;
    Value2: string;
    Answer2: string;
}
const Body: React.FC = () => {
    const [questions, setQuestions] = useState<any[]>([]);
    const [values, setValues] = useState<{ [key: string]: string }>({});

    const handleChange = (event: React.ChangeEvent<HTMLInputElement>) => {
        const { name, value } = event.target;
        setValues((prevValues) => ({
            ...prevValues,
            [name]: value,
        }));
    };
    const csvData = `
id,Question,Value1,Answer1,Value2,Answer2
1,あなたの好きな色は何ですか？,red,赤,blue,青
2,あなたの好きな動物は何ですか？,cat,猫,dog,犬
`;

    useEffect(() => {
        // CSVデータをパースする
        const parsedData = Papa.parse<Question>(csvData, { header: true });
        setQuestions(parsedData.data);
    }, []);

    return (
        <Container fixed>
            <Box component="section" sx={{ bgcolor: 'background.paper', p: 5, width: '90vw', height: '100vh' }}>
                <Typography variant="h4">ソーシャルスタイル診断</Typography>
                {questions.map((question, index) => (
                    <FormControl key={index} component="fieldset" sx={{ mb: 3 }}>
                        <FormLabel component="legend">{question.Question}</FormLabel>
                        <RadioGroup
                            row
                            aria-labelledby={`question-${index}`}
                            name={question.id}
                            value={values[question.id] || ''}
                            onChange={handleChange}
                        >
                            <FormControlLabel value={question.Value1} control={<Radio />} label={question.Answer1} />
                            <FormControlLabel value={question.Value2} control={<Radio />} label={question.Answer2} />
                        </RadioGroup>
                    </FormControl>
                ))}
                {/* <Button variant="contained" size="medium" onClick={calculateResults}>
                    <Typography variant="body1">診断する</Typography>
                </Button> */}
                <Typography variant="body1">This is an example page using Container and Box.</Typography>
            </Box>
        </Container>
    );
};
